import React, { Component } from 'react'
import timeConverter from '../../Calculation/timeConverter'
import './Weeksummary.css'

class Weeksummary extends Component {
  render() {
      const {weekdata} = this.props 
    
      let ArrayData =[]
      
    

      for(let i=0;i<weekdata.length;i++){
          console.log(timeConverter(weekdata[i].time).replace(' 0:0','').toUpperCase())
        console.log(weekdata[i].temperatureMax)
        console.log(weekdata[i].temperatureMin)
        ArrayData.push({
            day:timeConverter(weekdata[i].time).replace(' 0:0','').toUpperCase(),
            max:weekdata[i].temperatureMax,
            min:weekdata[i].temperatureMin

        })

        
      }
    ArrayData.forEach(e=>{
        console.log(e)
    })


    
    return (
      <React.Fragment>
      <Individualweek pass= {ArrayData} />
        
      </React.Fragment>
    )
  }
}




class Individualweek extends Component{

    render(){
        const {pass} = this.props
        const eachitem = pass.map((e)=>{
            return(
                <div key={e.day} className='weekSummary'>
                   <p>{e.day}</p>
                   <p> <span> {e.max}<sup>.C</sup></span>  </p>
                   <p><span>{e.min}<sup>.C</sup></span>  </p>
               </div>
            )
        })
        
        return(
            <>
                {eachitem}
            </>
           
        )
    }
}



export default Weeksummary

      
    
 